export const listaAcessorios = [
  'Traquias',
  'Blender',
  'Umidificador',
  'Jarra',
  'Vlvula exalatria',
  'Membrana',
  'Conector "Y"',
  'Conector de Dreno',
  'Sensor de Fluxo',
  'Sensor de temperatura',
  'Linha de presso proximal brao articulado',
  'Pulmo de Teste',
  'Pedestral',
  'Brao articulado',
  'Mangueira de Oxignio (verde)',
  'Mangueira de Ar Medicinal (amarela)',
  'Cabo de Fora / Fonte'
];

export function Acessorio (acessorio) {
  return Object.assign({
    descricao: '',
    acompanha: false,
    quantidade: 0,
    estado_de_conservacao: ''
  }, acessorio)
}

export function listaFormAcessorios (acessorios) {
  return listaAcessorios.sort((a,b) => a.localeCompare(b)).map(item => {
    return Acessorio(acessorios.find(acessorio => acessorio.descricao === item) || {descricao: item})
  })
}